import React, { FC, ReactNode } from 'react'
import styled from 'styled-components'
import {
  grid,
  color,
  GridProps,
  LayoutProps,
  layout,
  flexbox,
  FlexboxProps,
  space,
  SpaceProps,
} from 'styled-system'

interface GridParams extends GridProps, LayoutProps, FlexboxProps, SpaceProps {
  children: ReactNode
  className?: string
  columns: number | string | string[]
  spacing?: number | string | string[]
}

const Box = styled.div<GridProps & LayoutProps & FlexboxProps & SpaceProps>`
  ${color}
  ${grid}
  ${layout}
  ${flexbox}
  ${space}
  gap: ${({ gridGap }) => gridGap};
`

const Grid: FC<GridParams> = ({
  className,
  children,
  columns,
  spacing,
  ...props
}) => {
  return (
    <Box
      className={className}
      display="grid"
      gridTemplateColumns={columns}
      gridGap={spacing}
      {...props}
    >
      {children}
    </Box>
  )
}
export default Grid
