import React, { FC, useState } from 'react'
import { Link } from 'gatsby'
import { StaticImage } from 'gatsby-plugin-image'
import { DropdownMobile, Grid, Icons, Text, Button } from 'components/theme'
import HeaderData from 'content/header.json'
import {
  DropdownItem,
  HeaderLogo,
  MenuMobileContainer,
  MenuMobileHeader,
  MenuMobilePanel,
  SubHeaderMobile,
} from './styled'
import { Box, Divider, Flex } from 'styles/globalStyle'
import { HeaderDropdown } from 'types/header'
import { IconsName } from 'components/theme/Icons'

const variants = {
  open: { right: 0, transition: { duration: 0.3, ease: 'easeInOut' } },
  closed: { right: '-100vw', transition: { duration: 0.3, ease: 'easeInOut' } },
}

const MenuMobile: FC = () => {
  const { subheader, nav } = HeaderData
  const [isOpen, setIsOpen] = useState(false)

  return (
    <MenuMobileContainer>
      <button onClick={() => setIsOpen((isOpen) => !isOpen)}>
        <Icons name="menu" width="1.5rem" height="1.5rem" />
      </button>
      <MenuMobilePanel animate={isOpen ? 'open' : 'closed'} variants={variants}>
        <MenuMobileHeader alignItems="center" justifyContent="space-between">
          <HeaderLogo to="/">
            <StaticImage
              src="../../assets/images/logo.png"
              alt="Audiplus logo"
            />
          </HeaderLogo>
          <Box onClick={() => setIsOpen((isOpen) => !isOpen)}>
            <Icons name="close" width="1.5rem" height="1.5rem" />
          </Box>
        </MenuMobileHeader>
        <Box padding="0 1rem">
          {nav.map((item, idx) => (
            <Box key={idx}>
              <DropdownMobile title={item.text}>
                <Flex flexDirection="column">
                  {item.dropdown.map((dropdownItem: HeaderDropdown, idx) => (
                    <Box key={idx} padding="0.5rem 0" color="gray.500">
                      {dropdownItem.link ? (
                        <Link to={dropdownItem.link}>{dropdownItem.title}</Link>
                      ) : dropdownItem.list ? (
                        <DropdownMobile title={dropdownItem.title} subitem>
                          {dropdownItem.list.map(
                            (
                              listItem: {
                                text: string
                                link?: string
                              },
                              index
                            ) => (
                              <DropdownItem
                                as={Link}
                                key={index}
                                to={listItem.link || '/'}
                              >
                                {listItem.text}
                              </DropdownItem>
                            )
                          )}
                        </DropdownMobile>
                      ) : null}
                    </Box>
                  ))}
                </Flex>
              </DropdownMobile>
            </Box>
          ))}
        </Box>
        <Divider m="2rem 0 1.5rem" />
        <Grid
          columns={['1fr 1fr']}
          spacing="0.75rem 1.5rem"
          padding="0 1rem"
          mb="2rem"
        >
          {subheader.list.map((item, idx) => (
            <SubHeaderMobile key={idx}>
              {item.isExternal ? (
                <a href={item.link}>
                  <Icons
                    name={item.icon as IconsName}
                    width="1.25rem"
                    height="1.25rem"
                    mr="0.5rem"
                    color="gray.500"
                  />
                  <Text fontWeight="500">{item.text}</Text>
                </a>
              ) : (
                <Link to={item.link}>
                  <Icons
                    name={item.icon as IconsName}
                    width="1.25rem"
                    height="1.25rem"
                    mr="0.5rem"
                    color="gray.500"
                  />
                  <Text fontWeight="500">{item.text}</Text>
                </Link>
              )}
            </SubHeaderMobile>
          ))}
        </Grid>
        <Box padding="0 1rem">
          <Button
            type="gradient"
            to="/appointment"
            size="xl"
            width="calc(100% - 2.5rem)"
          >
            <Icons
              name="calendar"
              width="1.25rem"
              height="1.25rem"
              mr="0.5rem"
            />
            Prendre rendez-vous
          </Button>
        </Box>
      </MenuMobilePanel>
    </MenuMobileContainer>
  )
}
export default MenuMobile
