import React, { FC, Fragment } from 'react'
import { Listbox } from '@headlessui/react'
import { SelectOptionType } from 'types/devicesPageTypes'
import { SelectButton, SelectOptions, SelectOption } from './styled'
import Icons from '../Icons'

type SelectParams = {
  placeholder: string
  options: SelectOptionType[]
  value: SelectOptionType | null
  onChange: (val: SelectOptionType) => void
}

const Select: FC<SelectParams> = ({
  placeholder,
  options,
  value,
  onChange,
}) => {
  return (
    <Listbox value={value} onChange={onChange}>
      <SelectButton>
        {value ? value.label : placeholder}
        <Icons name="arrow-down" width="1.25rem" height="1.25rem" as="span" />
      </SelectButton>
      <SelectOptions>
        <SelectOption value={null} as={Listbox.Option} className="reset">
          {placeholder}
        </SelectOption>
        {options.map((option) => (
          <Listbox.Option key={option.id} value={option} as={Fragment}>
            <SelectOption selected={value?.id === option.id}>
              {option.label}
            </SelectOption>
          </Listbox.Option>
        ))}
      </SelectOptions>
    </Listbox>
  )
}
export default Select
