import React, { FC, ReactNode, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Text } from './index'
import { Box, Divider, Flex } from 'styles/globalStyle'
import Icons from './Icons'

type AccordionParams = {
  title: string
  children: ReactNode
  withBorder?: boolean
}

const Accordion: FC<AccordionParams> = ({ children, title, withBorder }) => {
  const [expanded, setExpanded] = useState<boolean>(false)

  return (
    <Box
      maxWidth={['100%', '60%']}
      margin="0 auto"
      textAlign="left"
      style={{ cursor: 'pointer' }}
      onClick={() => setExpanded((prevState) => !prevState)}
    >
      <Flex justifyContent="space-between" pt="1.5rem">
        <Text size="lg" fontWeight="500" color="gray.900" mr="1rem">
          {title}
        </Text>
        <Box flexShrink="0">
          <Icons
            name={expanded ? 'minus-circle' : 'plus-circle'}
            width="1.25rem"
            height="1.25rem"
            color="primary.600"
          />
        </Box>
      </Flex>
      <AccordionContent isExpanded={expanded} content={children} />
      {withBorder && <Divider mt="1.5rem" />}
    </Box>
  )
}

const AccordionContent: FC<{ isExpanded: boolean; content: ReactNode }> = ({
  isExpanded,
  content,
}) => {
  return (
    <AnimatePresence initial={false}>
      <motion.div
        animate={isExpanded ? 'open' : 'closed'}
        variants={{
          open: { height: 'auto', opacity: 1, pointerEvents: 'unset' },
          closed: { height: 0, opacity: 0, pointerEvents: 'none' },
        }}
        transition={{ duration: 0.2, ease: 'easeInOut' }}
      >
        <Box p="0.5rem 2rem 0.5rem 0">{content}</Box>
      </motion.div>
    </AnimatePresence>
  )
}

export default Accordion
