import React, { CSSProperties, FC } from 'react'
import styled, { DefaultTheme, StyledProps } from 'styled-components'
import {
  color,
  ColorProps,
  variant,
  typography,
  TypographyProps,
  space,
  SpaceProps,
  flexbox,
  FlexProps,
} from 'styled-system'

export interface TextParams
  extends ColorProps,
    TypographyProps,
    SpaceProps,
    FlexProps,
    StyledProps<any> {
  size?: 'sm' | 'md' | 'lg' | 'xl'
  textTransform?: 'uppercase' | 'lowercase'
  style?: CSSProperties
}

const StyledText = styled.p<TextParams>`
  ${variant({
    prop: 'size',
    scale: 'text',
  })}
  ${variant({
    prop: 'textTransform',
    variants: {
      uppercase: {
        textTransform: 'uppercase',
      },
      lowercase: {
        textTransform: 'lowercase',
      },
    },
  })}
  ${color}
  ${typography}
  ${space}
  ${flexbox}
  color: ${({ theme, color }) => (color ? color : theme.colors.gray[500])};
  font-family: ${({ theme }) => theme.fontFamily.main};
`

const Text: FC<TextParams> = ({ children, size = 'md', as, ...props }) => {
  return (
    <StyledText size={size} as={as} {...props}>
      {children}
    </StyledText>
  )
}
export default Text
