import React, { FC } from 'react'
import { Link } from 'gatsby'
import { StaticImage } from 'gatsby-plugin-image'
import HeaderData from 'content/header.json'
import { IconsName } from 'components/theme/Icons'
import { Text, Icons, CustomPopover } from 'components/theme'
import { Flex } from 'styles/globalStyle'
import {
  StyledHeader,
  HeaderLogo,
  HeaderContainer,
  HeaderNav,
  SubHeader,
  SubHeaderList,
  SubHeaderListItem,
  SubHeaderContainer,
  HeaderMenu,
  HeaderButton,
} from './styled'
import MenuMobile from './MenuMobile'

const Header: FC = () => {
  const { subheader, nav } = HeaderData
  return (
    <StyledHeader>
      <SubHeader>
        <SubHeaderContainer>
          <Text fontWeight="500" color="white">
            {subheader.title}
          </Text>
          <SubHeaderList>
            {subheader.list.map((item, idx) => (
              <SubHeaderListItem key={idx}>
                {item.isExternal ? (
                  <a href={item.link}>
                    <Icons name={item.icon as IconsName} />
                    <Text fontWeight="500" color="white">
                      {item.text}
                    </Text>
                  </a>
                ) : (
                  <Link to={item.link}>
                    <Icons name={item.icon as IconsName} />
                    <Text fontWeight="500" color="white">
                      {item.text}
                    </Text>
                  </Link>
                )}
              </SubHeaderListItem>
            ))}
          </SubHeaderList>
        </SubHeaderContainer>
      </SubHeader>
      <HeaderMenu>
        <HeaderContainer as="div">
          <Flex>
            <HeaderLogo to="/">
              <StaticImage
                src="../../assets/images/logo.png"
                alt="Audiplus logo"
              />
            </HeaderLogo>
            <HeaderNav>
              {nav.map((item, idx) => (
                <CustomPopover
                  key={idx}
                  buttonText={item.text}
                  dropdown={item.dropdown}
                  col={item.col}
                  minWidth={item.minWidth}
                  fullWidth={item.fullWidth}
                />
              ))}
            </HeaderNav>
          </Flex>
          <HeaderButton type="gradient" to="/appointment">
            <Icons
              name="calendar"
              width="1.25rem"
              height="1.25rem"
              mr="0.5rem"
            />
            Prendre rendez-vous
          </HeaderButton>
          <MenuMobile />
        </HeaderContainer>
      </HeaderMenu>
    </StyledHeader>
  )
}
export default Header
