import React, { FC } from 'react'
import { Link } from 'gatsby'
import { Popover } from '@headlessui/react'
import { Box, Flex, Container } from 'styles/globalStyle'
import { StyledPanel, StyledPanelContent, MenuLink, MenuSubLink } from './style'
import { Grid, Icons, Text } from 'components/theme'
import { HeaderDropdown } from 'types/header'

type PopoverPanelParams = {
  containerCol: number | string[] | string
  data: HeaderDropdown[]
  eventEnter: () => void
  eventLeave: () => void
  minWidth?: string
  fullWidth?: boolean
}

const PopoverPanel: FC<PopoverPanelParams> = ({
  containerCol,
  data,
  eventEnter,
  eventLeave,
  minWidth,
  fullWidth,
}) => {
  return (
    <Popover.Panel static onMouseEnter={eventEnter} onMouseLeave={eventLeave}>
      <StyledPanel
        fullwidth={fullWidth?.toString()}
        initial={{ opacity: 0, y: -10, x: 0 }}
        animate={{ opacity: 1, y: 0, x: 0 }}
        exit={{ opacity: 0, y: -10, x: 0 }}
        transition={{ duration: 0.3 }}
      >
        {fullWidth ? (
          <Container>
            <StyledPanelContent
              columns={containerCol}
              spacing={containerCol === '1fr' ? 0 : '2rem'}
            >
              {data.map((item, idx) => {
                if (item.list) {
                  return (
                    <Box key={idx}>
                      <Text
                        size="sm"
                        color="primary.700"
                        fontWeight="500"
                        mb="1.5rem"
                      >
                        {item.title}
                      </Text>
                      <Grid
                        columns={item.columns || ['1fr']}
                        spacing={item.columns ? '0 3rem' : '0'}
                      >
                        {item.list.map((listItem, id) => {
                          return (
                            <MenuSubLink key={id} to={listItem.link || '/'}>
                              <Icons
                                name="arrow-right"
                                width="1.5rem"
                                height="1.5rem"
                                className="icon"
                              />
                              <Text fontWeight="500" color="gray.900">
                                {listItem.text}
                              </Text>
                            </MenuSubLink>
                          )
                        })}
                      </Grid>
                    </Box>
                  )
                } else {
                  return (
                    <MenuLink key={idx} to={item.link || '/'}>
                      {item.title}
                    </MenuLink>
                  )
                }
              })}
            </StyledPanelContent>
          </Container>
        ) : (
          <StyledPanelContent
            minWidth={minWidth}
            columns={containerCol}
            spacing={containerCol === '1fr' ? 0 : '2rem'}
          >
            {data.map((item, idx) => {
              if (item.list) {
                return (
                  <Box key={idx}>
                    <Text
                      size="sm"
                      color="primary.700"
                      fontWeight="500"
                      mb="1.5rem"
                    >
                      {item.title}
                    </Text>
                    <Grid columns={item.columns || ['1fr']} spacing="0">
                      {item.list.map((ii, id) => (
                        <MenuSubLink key={id} to={item.link || '/'}>
                          <Icons
                            name="arrow-right"
                            width="1.5rem"
                            height="1.5rem"
                            className="icon"
                          />
                          <Text fontWeight="500" color="gray.900">
                            {ii.text}
                          </Text>
                        </MenuSubLink>
                      ))}
                    </Grid>
                  </Box>
                )
              } else {
                return (
                  <MenuLink key={idx} to={item.link || '/'}>
                    {item.title}
                  </MenuLink>
                )
              }
            })}
          </StyledPanelContent>
        )}

        <Link to="/appointment">
          <Flex
            bg="primary.25"
            py="1.75rem"
            alignItems="center"
            justifyContent="center"
            borderBottomLeftRadius="0.5rem"
            borderBottomRightRadius="0.5rem"
          >
            <Icons
              name="calendar"
              width="1rem"
              height="1rem"
              mr="0.5rem"
              color="primary.700"
            />
            <Text color="primary.700" fontWeight="500" size="sm">
              Prendre un rendez-vous en ligne
            </Text>
          </Flex>
        </Link>
      </StyledPanel>
    </Popover.Panel>
  )
}
export default PopoverPanel
