/* eslint-disable @typescript-eslint/no-var-requires */
/* eslint-disable no-undef */
exports.linkResolver = (doc) => {
  // URL for a center page
  if (doc.type === 'centers') {
    return `/center/${doc.uid}`
  }

  // URL for a article page
  if (doc.type === 'articles') {
    return `/article/${doc.uid}`
  }

  if (doc.type === 'appointment_page') {
    return '/appointment'
  }

  if (doc.type === 'devices_page') {
    return '/devices'
  }

  if (doc.type === 'list_centers_page') {
    return '/centers'
  }

  // URL for a page type
  if (doc.type === 'basic_page') {
    return `/${doc.uid}`
  }

  // Backup for all other types
  return '/'
}
