import { GatsbyImage } from 'gatsby-plugin-image'
import styled, { createGlobalStyle } from 'styled-components'
import {
  border,
  BorderProps,
  color,
  ColorProps,
  flex,
  flexbox,
  FlexboxProps,
  FlexProps,
  grid,
  GridProps,
  layout,
  LayoutProps,
  position,
  PositionProps,
  space,
  SpaceProps,
  typography,
  TypographyProps,
} from 'styled-system'

export const GlobalStyle = createGlobalStyle`
  html {
    font-size: 0.875rem;
    @media screen and (min-width: ${({ theme }) => theme.bp.md}) {
      font-size: 1rem;
    }
  }
  
  body {
    margin: 0;
    font-family: ${({ theme }) => theme.fontFamily.main}
  }

  h1, h2, h3, h4, p, ul, li {
    margin: 0;
    padding: 0;
  }

  ul {
    list-style: none;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  button {
    outline: none;
    border: inherit;
    padding: 0;
    background: inherit;
  }
`

export const Container = styled.section<
  ColorProps &
    SpaceProps &
    TypographyProps &
    LayoutProps &
    FlexboxProps &
    GridProps
>`
  ${color}
  ${space}
  ${typography}
  ${layout}
  ${flexbox}
  ${grid}
  padding-left: 1rem;
  padding-right: 1rem;
  width: calc(100% - 2rem);
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: ${({ theme }) => theme.bp.sm}) {
    padding-left: 2rem;
    padding-right: 2rem;
    width: calc(100% - 4rem);
  }

  @media screen and (min-width: ${({ theme }) => theme.bp.lg}) {
    padding-left: 4rem;
    padding-right: 4rem;
    width: calc(100% - 8rem);
  }

  @media screen and (min-width: ${({ theme }) => theme.bp.xl}) {
    padding-left: 7rem;
    padding-right: 7rem;
    width: calc(100% - 14rem);
  }
`

export const StyledMain = styled.main`
  padding-top: 68px;
  @media screen and (min-width: ${({ theme }) => theme.bp.md}) {
    padding-top: 73px;
  }
  @media screen and (min-width: ${({ theme }) => theme.bp.xl}) {
    padding-top: 131px;
  }
`

export const Flex = styled.div<
  FlexProps & FlexboxProps & SpaceProps & BorderProps & ColorProps & LayoutProps
>`
  ${flex}
  ${flexbox}
  ${space}
  ${border}
  ${color}
  ${layout}
  display: flex;
`

export const Hero = styled.div`
  height: 100%;
  align-items: center;
  padding: 6rem 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2rem;

  .gatsby-image-wrapper {
    width: 100%;
    height: 100%;
  }
  @media screen and (max-width: ${({ theme }) => theme.bp.sm}) {
    h1 {
      font-size: 2rem;
      line-height: 2.5rem;
    }
  }
  @media screen and (min-width: ${({ theme }) => theme.bp.lg}) {
    min-height: calc(85vh - (131px + 15rem));
    height: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 4rem;
    .gatsby-image-wrapper {
      max-height: 448px;
    }
  }
`

export const Box = styled.div<
  LayoutProps &
    TypographyProps &
    ColorProps &
    FlexboxProps &
    SpaceProps &
    BorderProps &
    PositionProps
>`
  ${layout}
  ${typography}
  ${color}
  ${flexbox}
  ${space}
  ${border}
  ${position}
`

export const Circle = styled.div<LayoutProps & ColorProps & SpaceProps>`
  ${layout}
  ${color}
  ${space}
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
`

export const Divider = styled(Box)`
  width: 100%;
  height: 1px;
  background: ${({ theme }) => theme.colors.gray[200]};
`

export const Card = styled(Box)<PositionProps>`
  ${position}
  box-shadow: ${({ theme }) => theme.boxShadow.card};
  border-radius: 1rem;
`

export const DottedLine = styled(Flex)`
  justify-content: space-between;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    bottom: 0.4rem;
    height: 0;
    width: 100%;
    z-index: -1;
    border-bottom: 2px dotted ${({ theme }) => theme.colors.gray[500]};
  }
  & > p {
    background: white;
  }
`

export const ImageBorder = styled(GatsbyImage)`
  border-radius: 1rem;
  border: 2px solid ${({ theme }) => theme.colors.primary[500]};
`
