import React, { FC, MouseEvent, ReactNode } from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'
import {
  border,
  BorderProps,
  flexbox,
  FlexboxProps,
  layout,
  LayoutProps,
  space,
  SpaceProps,
  variant,
} from 'styled-system'

interface ButtonParams
  extends SpaceProps,
    FlexboxProps,
    LayoutProps,
    BorderProps {
  children: ReactNode
  href?: string
  to?: string
  type?: 'gradient' | 'light' | 'primary' | 'white' | 'borderWhite' | 'link'
  size?: 'md' | 'lg' | 'xl' | 'xxl'
  fullWidth?: boolean
  onClick?: () => void
}

const StyledButton = styled.a<ButtonParams>`
  ${variant({
    prop: 'size',
    scale: 'buttons.size',
  })}
  ${variant({
    prop: 'type',
    scale: 'buttons.type',
  })}
  ${space}
  ${flexbox}
  ${layout}
  ${border}
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 1.875rem;
  font-weight: 500;
  box-shadow: ${({ theme, type }) =>
    type !== 'link' ? theme.boxShadow.button : 'unset'};
  font-family: ${({ theme }) => theme.fontFamily.main};
  cursor: pointer;
  text-align: center;
`

const Button: FC<ButtonParams> = ({
  children,
  type = 'primary',
  size = 'lg',
  href,
  to,
  ...props
}) => {
  return (
    <StyledButton
      size={size}
      type={type}
      {...props}
      to={to}
      as={href ? 'a' : to ? Link : 'button'}
      href={href}
    >
      {children}
    </StyledButton>
  )
}

export default Button
