import React, { FC, Fragment } from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api'

type MapCoord = { lat: number; lng: number }
type MapParams = {
  height?: string
  zoom?: number
  position?: MapCoord
  marker?: MapCoord
}

const Map: FC<MapParams> = ({ height, zoom, position, marker }) => {
  const centersCoord = useStaticQuery(graphql`
    query {
      allPrismicCenters {
        nodes {
          data {
            coordinates {
              latitude
              longitude
            }
          }
        }
      }
    }
  `)
  const allCentersMarkers: { latitude: number; longitude: number }[] =
    centersCoord.allPrismicCenters.nodes.map(
      (item: { data: { coordinates: MapCoord } }) => item.data.coordinates
    )
  const icon =
    'https://res.cloudinary.com/dzigftse0/image/upload/v1648723333/marker_pkaraa.svg'
  const defaultProps = {
    center: {
      lat: position?.lat || 48.80767,
      lng: position?.lng || 2.2403,
    },
    zoom: zoom || 5.75,
  }

  return (
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    <LoadScript googleMapsApiKey={process.env.GOOGLE_MAPS_API_KEY || ''}>
      <GoogleMap
        mapContainerStyle={{ height: height || '528px' }}
        center={defaultProps.center}
        zoom={defaultProps.zoom}
      >
        {marker ? (
          <Marker
            icon={icon}
            position={{
              lat: marker.lat,
              lng: marker.lng,
            }}
          />
        ) : (
          <Fragment>
            {allCentersMarkers.map((marker, idx) => (
              <Marker
                key={idx}
                icon={icon}
                position={{
                  lat: marker.latitude,
                  lng: marker.longitude,
                }}
              />
            ))}
          </Fragment>
        )}
      </GoogleMap>
    </LoadScript>
  )
}

export default Map
