import React, { FC, ReactNode, useState } from 'react'
import { AnimatePresence } from 'framer-motion'
import { Text } from '../index'
import { Box, Divider, Flex } from 'styles/globalStyle'
import Icons from '../Icons'
import { DropdownList } from './styled'

type DropdownParams = {
  title: string
  children: ReactNode
  withBorder?: boolean
  subitem?: boolean
}

const DropdownMobile: FC<DropdownParams> = ({
  children,
  title,
  withBorder,
  subitem,
}) => {
  const [expanded, setExpanded] = useState<boolean>(false)

  return (
    <Box
      margin="0 auto"
      textAlign="left"
      style={{ cursor: 'pointer' }}
      pl={subitem ? '0.75rem' : 0}
    >
      <Flex
        justifyContent="space-between"
        pt={subitem ? 0 : '1rem'}
        mb="0.5rem"
        onClick={() => setExpanded((prevState) => !prevState)}
      >
        <Text
          size={subitem ? 'md' : 'lg'}
          fontWeight="500"
          color={subitem ? 'gray.500' : 'gray.900'}
          mr="1rem"
        >
          {title}
        </Text>
        <Box flexShrink="0">
          <Icons
            name={expanded ? 'arrow-up' : 'arrow-down'}
            width="1.25rem"
            height="1.25rem"
          />
        </Box>
      </Flex>
      <DropdownContent isExpanded={expanded} content={children} />
      {withBorder && <Divider mt="1.5rem" />}
    </Box>
  )
}

const DropdownContent: FC<{
  isExpanded: boolean
  content: ReactNode
}> = ({ isExpanded, content }) => {
  return (
    <AnimatePresence initial={false}>
      <DropdownList
        animate={isExpanded ? 'open' : 'closed'}
        variants={{
          open: {
            height: 'auto',
            opacity: 1,
            pointerEvents: 'unset',
            display: 'block',
          },
          closed: {
            height: 0,
            opacity: 0,
            pointerEvents: 'none',
            display: 'none',
          },
        }}
        transition={{ duration: 0.2, ease: 'easeInOut' }}
      >
        {content}
      </DropdownList>
    </AnimatePresence>
  )
}

export default DropdownMobile
