import React, { FC, useState } from 'react'
import { Popover } from '@headlessui/react'
import { AnimatePresence } from 'framer-motion'
import { Box, Flex } from 'styles/globalStyle'
import Text from '../Text'
import Icons from '../Icons'
import PopoverPanel from './PopoverPanel'
import { HeaderDropdown } from 'types/header'

type CustomPopoverParams = {
  buttonText: string
  col: number | string[] | string
  dropdown: HeaderDropdown[]
  minWidth?: string
  fullWidth?: boolean
}

const CustomPopover: FC<CustomPopoverParams> = ({
  buttonText,
  dropdown,
  col,
  minWidth,
  fullWidth,
}) => {
  const [isOpen, setOpen] = useState(false)
  let timeout: NodeJS.Timeout | null = null
  const timeoutDuration = 200

  const onMouseEnter = () => {
    timeout && clearTimeout(timeout)
    setOpen(true)
  }
  const onMouseLeave = () => {
    isOpen && (timeout = setTimeout(() => setOpen(false), timeoutDuration))
  }

  return (
    <Popover className="popover-item">
      <Box position={fullWidth ? 'static' : 'relative'}>
        <Popover.Button
          as={Flex}
          alignItems="center"
          onMouseEnter={() => onMouseEnter()}
          onMouseLeave={() => onMouseLeave()}
        >
          <Text fontWeight="500" color="gray.900">
            {buttonText}
          </Text>
          <Icons
            name={isOpen ? 'arrow-up' : 'arrow-down'}
            width="1.25rem"
            height="1.25rem"
            ml="0.5rem"
          />
        </Popover.Button>
        <AnimatePresence exitBeforeEnter>
          {dropdown && isOpen && (
            <PopoverPanel
              data={dropdown}
              containerCol={col}
              eventEnter={onMouseEnter}
              eventLeave={onMouseLeave}
              minWidth={minWidth}
              fullWidth={fullWidth}
            />
          )}
        </AnimatePresence>
      </Box>
    </Popover>
  )
}
export default CustomPopover
