import React, { FC, ReactNode } from 'react'
import styled from 'styled-components'
import {
  color,
  ColorProps,
  space,
  SpaceProps,
  typography,
  TypographyProps,
  variant,
} from 'styled-system'

export interface TitleParams extends ColorProps, TypographyProps, SpaceProps {
  children?: ReactNode
  size?: 'xs' | 'sm' | 'md' | 'lg'
  as?: any
  dangerouslySetInnerHTML?: any
}

const StyledTitle = styled.h1`
  ${variant({
    prop: 'size',
    scale: 'title',
  })}
  ${color}
  ${typography}
  ${space}
  color: ${({ theme, color }) => (color ? color : theme.colors.gray[900])};
`

const Title: FC<TitleParams> = ({ children, size = 'md', as, ...props }) => {
  return (
    <StyledTitle size={size} as={as} {...props}>
      {children}
    </StyledTitle>
  )
}
export default Title
